import { useLocation, useParams, useSearchParams } from "react-router-dom";
import UniversalDepartmentMiddleHead from "../../../MembersTab/UniversalDepartmentMiddleHead/UniversalDepartmentMiddleHead";
import {
  assestsAcademicDepartmentUrl,
  assestsStoreUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";
import SquareTabWrapper from "../../Ui/Tab/Square/SquareTabWrapper";
import SquareTabCard from "../../Ui/Tab/Square/SquareTabCard";
import RectangleTabWrapper from "../../Ui/Tab/Rectangle/RectangleTabWrapper";
import RectangleTabCard from "../../Ui/Tab/Rectangle/RectangleTabCard";
import { useInstituteUserDepartmentDashboard } from "../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import Loading from "../../Loader/Loading";
import { Suspense } from "react";
import InstituteAcademicDepartmentStaff from "../../InstituteMember/InstituteAcademicDepartment/InstituteAcademicDepartmentDetail/InstituteAcademicDepartmentStaff/InstituteAcademicDepartmentStaff";
import InstituteAcademicDepartmentStudent from "../../InstituteMember/InstituteAcademicDepartment/InstituteAcademicDepartmentDetail/InstituteAcademicDepartmentStudent/InstituteAcademicDepartmentStudent";

const AcademicDepartmentManagerProfile = ({ did }) => {
  const getQuery = useLocation();
  const params = useParams();
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();

  let measure = document.getElementById("7892bdsb")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 870 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 870 ? 1 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }

  const { instituteUserDepartmentDashboard } =
    useInstituteUserDepartmentDashboard({
      data: {
        did: getQuery.state?.onClickValue?._id,
      },
      skip: !getQuery.state?.onClickValue?._id,
    });

  return (
    <>
      <UniversalDepartmentMiddleHead
        coverKey={instituteUserDepartmentDashboard?.cover}
        coverId={instituteUserDepartmentDashboard?.coverId}
        defaultCover={`${assestsAcademicDepartmentUrl}/cover.png`}
        textLarge={instituteUserDepartmentDashboard?.dName}
        batchStatus="false"
        // textSmall={`${instituteUserDepartmentDashboard?.userBatch?.batchName}`}
        count1={instituteUserDepartmentDashboard?.classCount}
        count1label={t("classes")}
        count3={instituteUserDepartmentDashboard?.studentCount}
        count2label={t("staff")}
        count2={instituteUserDepartmentDashboard?.staffCount}
        count3label={t("students")}
        viewAs={true}
        redirectUrl={`/q/${params.username}/member/staff/academic/department/${params.name}?a=site`}
        carryParentState={getQuery.state}
      />
      <UserMemberTabsWrapper
        customStyle={{
          marginTop: "-1.5rem",
          borderTopLeftRadius: "1.5rem",
          borderTopRightRadius: "1.5rem",
        }}
      >
        <RectangleTabWrapper>
          {tabList.map((tab) => (
            <RectangleTabCard
              key={tab?.id}
              label={tab.label}
              isLink
              isActive={
                tab.id === "menu" && !searchParams.get("type")
                  ? true
                  : searchParams.get("type") === tab.id
                  ? true
                  : false
              }
              parentState={getQuery.state}
              url={`/q/${params.username}/member/staff/academic/department/${params.name}${tab.urlKey}`}
              isAuthorized
            />
          ))}
        </RectangleTabWrapper>
        {!searchParams.get("type") ? (
          <SquareTabWrapper dynamicId="7892bdsb">
            <SquareTabCard
              label="class"
              iconUrl={`${assestsStoreUrl}/unit.svg`}
              url={`/q/${params.username}/member/staff/academic/department/${params.name}?type=cls`}
              state={getQuery.state}
              isAuthorized
              // isVisible={tabManage ?? {}}
              isVisible={{
                store_unit: true,
              }}
              authorizedKey="store_unit"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />

            <SquareTabCard
              label="masters"
              iconUrl={`${assestsStoreUrl}/individual.svg`}
              url={`/q/${params.username}/member/staff/academic/department/${params.name}?type=master`}
              state={getQuery.state}
              isAuthorized
              // isVisible={tabManage ?? {}}
              isVisible={{
                individual_staff: true,
              }}
              authorizedKey="individual_staff"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />

            <SquareTabCard
              label="batches"
              iconUrl={`${assestsStoreUrl}/stock.svg`}
              url={`/q/${params.username}/member/staff/academic/department/${params.name}?type=batch`}
              state={getQuery.state}
              isAuthorized
              // isVisible={tabManage ?? {}}
              isVisible={{
                stock: true,
              }}
              authorizedKey="stock"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            {/* <SquareTabCard
              label="staffs"
              iconUrl={`${assestsStoreUrl}/issue.svg`}
              url={`/q/${params.username}/member/staff/academic/department/${params.name}?type=staff`}
              state={getQuery.state}
              isAuthorized
              // isVisible={tabManage ?? {}}
              isVisible={{
                issue_register: true,
              }}
              authorizedKey="issue_register"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />

            <SquareTabCard
              label="students"
              iconUrl={`${assestsUserFinanceUrl}/student.svg`}
              url={`/q/${params.username}/member/staff/academic/department/${params.name}?type=student`}
              state={getQuery.state}
              isAuthorized
              // isVisible={tabManage ?? {}}
              isVisible={{
                consumption: true,
              }}
              authorizedKey="consumption"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            /> */}
          </SquareTabWrapper>
        ) : searchParams.get("type") === "staff" ? (
          <Suspense fallback={<Loading />}>
            <InstituteAcademicDepartmentStaff
              did={did}
              openAs="DEPARTMENT_HEAD"
            />
          </Suspense>
        ) : searchParams.get("type") === "student" ? (
          <Suspense fallback={<Loading />}>
            <InstituteAcademicDepartmentStudent
              did={did}
              openAs="DEPARTMENT_HEAD"
            />
          </Suspense>
        ) : null}
      </UserMemberTabsWrapper>
    </>
  );
};

export default AcademicDepartmentManagerProfile;

const tabList = [
  {
    label: "menu",
    id: "menu",
    urlKey: "",
  },
  {
    label: "staffs",
    id: "staff",
    urlKey: "?type=staff",
  },
  {
    label: "students",
    id: "student",
    urlKey: "?type=student",
  },
];
